export function addFav(item: string) {
  const favs = JSON.parse(localStorage.getItem("tp_favs") || "[]");
  if (!favs.includes(item)) favs.push(item);
  localStorage.setItem("tp_favs", JSON.stringify(favs));
}

export function getFavs(): string[] {
  return JSON.parse(localStorage.getItem("tp_favs") || "[]");
}
